::-webkit-scrollbar {
  width: 2px;
  height: 1px;
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  background-color: #fff;
  border-radius: 12px;
}

:root {
  --color-black: rgba(28, 28, 43, 1);
  --color-black-opacity-1: rgba(28, 28, 43, 0.1);
  --color-black-opacity-3: rgba(28, 28, 43, 0.3);
  --color-black-opacity-04: rgba(28, 28, 43, 0.04);
  --color-outlined-input-background: rgba(243, 248, 255, 1);
  --color-outlined-input-border: rgba(215, 232, 255, 1);
  --color-warning: rgba(242, 201, 76, 1);
  --color-error-main: rgba(235, 87, 87, 1);
  --color-error-light: rgba(235, 87, 87, 0.1);
}
