.container {
  background-image: url("./assets/background.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.background {
  position: absolute;
  z-index: -1;
  height: 100%;
  width: 100%;
  background: linear-gradient(124.53deg, #ece9e6 0%, #ffffff 65.51%);
}
